body{
    /* height: 100vh;
    width: 100vw;
    overflow: hidden; */
}
.canvas-box{
    width: 100%;
    height: 85vh;
    background: #f0f8ff;
    padding: 15px;
    border-radius: 20px;
}

#canvas{
    border: 1px solid #2f74b0;
    border-radius: 20px;

}
.canvas-display-container{
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 50%;
    width: 70%;
    margin: auto;
    position: relative;
}
.canvas-display-container .canvas-container{
    position: absolute!important;
    top: 0;
}

.canvas-buttons{
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-top: 20px;
}
.bottom-nav{
    position: absolute;
    left: 0;
    background: #fff;
    width: 100vw;
    height: 140px;
    width: 100vw;
    z-index: 1;
    transition: bottom 2s;
    bottom: -140px;
}
.bottom-nav.active{
    bottom: 0;
}
.bottom-nav .ham-circle{
    background: #fff;
    width: 50px;
    height: 50px;
    padding: 55px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: self-end;
    margin: auto;
    position: absolute;
    left: calc(50%-10px);
    left: 50%;
    top: -60%;
    transform: translate(-50%, 30%);
    height: 100px;
    z-index: -1;
    box-shadow: 1px 20px 25px;
}
.ham-circle svg{
    height: 1em!important;
    width: 1em!important;
}
.bottom-nav span svg{
    margin-bottom: 20px;
    font-size: 18px;
    color: #ccc;
}
.nav-buttons{
    width: 100vw;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    z-index: 11;
    background: #fff;
}
.nav-button{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100px;
    width: 100px;
    background: #dcefff;
    border-radius: 10px;
    font-size: 18px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    font-weight: 300;
}
.nav-button--secondary{
    color: #555;
    width: 100%;
    border-bottom: 1px solid #ccc;
    border-radius: 0;
    background: #fff;
}
.nav-button--secondary:first-child{
    border-top-left-radius: 15px;
}
.nav-button--secondary:last-child{
    border-bottom-left-radius: 15px;
}
.nav-button svg{
    height: 1.5em;
}
.nav-button span{
    margin-top: 10px;
}

.sidenav{
    position: absolute;
    right:0;
    top: 50%;
    transform: translate(0, -50%);
    background: #fff;
    width: 120px;
    height: -webkit-max-content;
    height: max-content;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    box-shadow: 0 1px 2px rgba(0,0,0,0.07), 
                0 2px 4px rgba(0,0,0,0.07), 
                0 4px 8px rgba(0,0,0,0.07), 
                0 8px 16px rgba(0,0,0,0.07),
                0 16px 32px rgba(0,0,0,0.07), 
                0 32px 64px rgba(0,0,0,0.07);
                border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}


.sidenav-container{
}

.sidenav.active, .sidenav-container.active{
    display: block;
}

.bottom-slide-container{
    display: none;
    width: 100vw;
    height: 100vh;
    position: absolute;
    bottom: 0;
    left: 0;
    background: rgb(0, 0, 0, .3);
    justify-content: center;
    align-items: flex-end;
    z-index: 1;
}
.bottom-slide{
    width: 100%;
    height: 80%;
    background: #333;
    border-top-left-radius: 45px;
    border-top-right-radius: 45px;
    z-index: 1;
    overflow: auto
}

.bottom-slide-tools{
    display: none;
    justify-content: space-around;
    align-items: center;
}

.img-container{
    flex-wrap: wrap;
}
.img-container div, .upload-img-container div{
    height: 200px;
    width: 300px;
    margin-top: 25px;
}
.upload-img-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}


.img-container img, .upload-img-container img{
    height: 100%;
    width: 100%;
    border-radius: 15px;
}

.bottom-slide-container.active, .bottom-slide-tools.active{
    display: flex;
}
.text-container.active,.fonts-container.active,.upload-container.active{
    display: block;
}

.update-container{
    flex-direction: column;
    align-items: stretch;
    justify-content: space-between;
    height: 100%;
}


.range-container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
    width: 100%;
    flex-direction: column;
}

.range-container input{
    width: 60%;
}

.range-container .range-display{
    border: 1px solid #fff;
    padding: 5px 10px;
    border-radius: 5px;
    color: #fff;
    margin-bottom: -5px;
}

.button-container{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
}

.update-panel{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}
.update-panel-container{
    display: flex;
    justify-content: center;
    align-items: center;
}

.update-panel-container div{
    margin-left: 15px;
    border-radius: 50%;
    height: 90px;
    width: 90px;
    border: 1px solid #fff;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}
.update-panel-container div.active{
    border: 3px solid #fff;
}
.update-panel-container div:first-child{
    margin-left: 0;
}
.update-panel img{
    height: 90%;
}
.text-element{
    background: #555;
    border-radius: 15px;
    font-size: 1rem;
    color: #fff;
    padding: 10px;
    margin-top: 10px;
}
.text-element:first-child{
    margin-top: 0;
}
.text-element.large{
    font-size: 2rem;
    padding: 20px;
    font-weight: 700;
}
.text-element.medium{
    font-size: 20px;
    padding: 15px;
    font-weight: 400;
}
.text-element.small{
    font-size: 16px;
    padding: 12px;
    font-weight: 400;
}


@media(max-width:767px){
    .img-container div, .upload-img-container div{
        height: 150px;
        width: unset;
    }
    .update-panel-container div{
        width: 70px;
        height: 70px;
    }
}


@media(max-width:500px){
    .bottom-nav .ham-circle{
        padding: 50px;
        top: -50%;
    }

    .bottom-slide h3{
        font-size: 16px;
    }

    .text-element.large{
        font-size: 24px;
    }

    .text-element.medium{
        font-size: 16px;
    }

    .text-element.small{
        font-size: 12px;
    }

    .sidenav{
        width: 90px;
    }
    .nav-button {
        font-size: 16px;
        height: 80px;
        width: 80px;
    }
    .nav-button--secondary{
        width: 100%;
    }
    .update-panel-container div{
        width: 60px;
        height: 60px;
    }

    .range-container input{
        width: 100%;
    }
}

@media(max-width:350px){
    .sidenav{
        width: 70px;
    }
    .nav-button {
        font-size: 12px;
        height: 65px;
        width: 65px;
    }
}
