    }
  }
.book {
  position: relative;
  width: 350px;
  height: 470px;
  background-color: #ddd;
  border-radius: 5px;
  box-shadow: -10px 3px 25px rgb(20 20 20);
  margin-top: 60px;
  cursor: pointer;
}

.book:first-child{
  /* margin-top: 0; */
}
.book::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 100%;
  background-image: linear-gradient(to right, #666, transparent);
  left: 10px;
}
.book::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 100%;
  background-image: linear-gradient(to right, #aaa 3%, #aaa 50%);
  left: 0;
  box-shadow: 1px 3px 5px rgba(20, 20, 20, 0.3);
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.book .text {
  position: absolute;
  top: 30px;
  left: 35px;
  left: 0;
  width: 100%;
  margin: auto;
}
.book h1 {
  font-size: 22px;
  color: #666;
  text-align: center;
  margin: 0;
  letter-spacing: 1px;
  font-weight: 700;
}
.book p {
  color: #777;
  font-size: 26px;
  text-align: center;
  margin: 0;
  letter-spacing: 3px;
  font-weight: 300;
}
.book .dots {
  position: absolute;
  bottom: 20px;
  height: -webkit-max-content;
  height: max-content;
  right: 20px;
  border-radius: 2px;
  display: flex;
  flex-direction: column;
}
.book .dot {
  width: 13px;
  height: 13px;
  background: #777;
  border-radius: 50%;
  margin-top: 5px;
}
.book .img-container{
  width: 80%;
  height: 250px;
  margin: auto;
  margin-top: 40px;
}

.book .img-container img{
  height: 100%;
  width: 100%;
}


@media(max-width:500px){
  .book{
      width: 100%;
  }
}

.react-contextmenu {
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: 0.25rem;
    color: #373a3c;
    font-size: 16px;
    margin: 2px 0 0;
    min-width: 160px;
    outline: none;
    opacity: 0;
    padding: 5px 0;
    pointer-events: none;
    text-align: left;
    transition: opacity 250ms ease !important;
  }
  .react-contextmenu-wrapper{
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .react-contextmenu.react-contextmenu--visible {
    opacity: 1;
    pointer-events: auto;
    z-index: 9999;
  }
  
  .react-contextmenu-item {
    background: 0 0;
    border: 0;
    color: #373a3c;
    cursor: pointer;
    font-weight: 400;
    line-height: 1.5;
    padding: 3px 20px;
    text-align: inherit;
    white-space: nowrap;
  }
  
  .react-contextmenu-item.react-contextmenu-item--active,
  .react-contextmenu-item.react-contextmenu-item--selected {
    color: #fff;
    background-color: #20a0ff;
    border-color: #20a0ff;
    text-decoration: none;
  }
  
  .react-contextmenu-item.react-contextmenu-item--disabled,
  .react-contextmenu-item.react-contextmenu-item--disabled:hover {
    background-color: transparent;
    border-color: rgba(0, 0, 0, 0.15);
    color: #878a8c;
  }
  
  .react-contextmenu-item--divider {
    border-bottom: 1px solid rgba(0, 0, 0, 0.15);
    cursor: inherit;
    margin-bottom: 3px;
    padding: 2px 0;
  }
  .react-contextmenu-item--divider:hover {
    background-color: transparent;
    border-color: rgba(0, 0, 0, 0.15);
  }
  
  .react-contextmenu-item.react-contextmenu-submenu {
    padding: 0;
  }
  
  .react-contextmenu-item.react-contextmenu-submenu > .react-contextmenu-item {
  }
  
  .react-contextmenu-item.react-contextmenu-submenu
    > .react-contextmenu-item:after {
    content: '';
    display: inline-block;
    position: absolute;
    right: 7px;
  }
  
  .example-multiple-targets::after {
    content: attr(data-count);
    display: block;
  }
  
